@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body, html {
  height: 100%;
  margin: 0;
}
.home-body {
	background: url('/images/landing.jpg');
	background-size: cover;
	background-repeat: no-repeat;
	height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	-webkit-animation: animatedBackground 5s linear 1;
	        animation: animatedBackground 5s linear 1;
	-webkit-animation-fill-mode: forwards;
	        animation-fill-mode: forwards;
}

@-webkit-keyframes animatedBackground {
	from {
		background-position: 50% 50%;
	}
	to {
		background-position: 0 0;
	}
}

@keyframes animatedBackground {
	from {
		background-position: 50% 50%;
	}
	to {
		background-position: 0 0;
	}
}

@-webkit-keyframes animatedBackgroundMobile {
	from {
		background-position: 0 0;
	}
	to {
		background-position: 39% 50%;
	}
}

@keyframes animatedBackgroundMobile {
	from {
		background-position: 0 0;
	}
	to {
		background-position: 39% 50%;
	}
}

#str {
	font-family: 'rockville_solidregular';
	color: white;
	text-align: center;
	font-size: 14vh;
}

h2 {
	font-family: "Courier New", Courier, monospace;
	color: white;
	text-align: center;
	font-size: 2rem;
}

#str {
	width: 43vw;
	height: 50vh;
}

@font-face {
	font-family: 'rockville_solidregular';
	src: url('/fonts/rockville_solid-webfont.woff2') format('woff2'), url('/fonts/rockville_solid-webfont.woff') format('woff');
	font-weight: normal;
	font-style: normal;
}

/* button */

div[class*=box] {
	height: 33.33%;
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 2rem;
}

.btn {
	line-height: 50px;
	height: 50px;
	text-align: center;
	width: 250px;
	cursor: pointer;
	color: #FFF;
	transition: all 0.5s;
	position: relative;
}

.btn span {
	z-index: 2;
	display: block;
	position: absolute;
	width: 100%;
	height: 100%;
}

.btn::before {
	content: '';
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 1;
	transition: all 0.5s;
	border: 1px solid rgba(255, 255, 255, 0.2);
	background-color: rgba(255, 255, 255, 0.1);
}

.btn::after {
	content: '';
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 1;
	transition: all 0.5s;
	border: 1px solid rgba(255, 255, 255, 0.2);
	background-color: rgba(255, 255, 255, 0.1);
}

.btn:hover::before {
	transform: rotate(180deg) scale(1.2, 1.2);
	background-color: rgba(255, 255, 255, 0);
}

.btn:hover::after {
	background-color: rgba(255, 255, 255, 0);
	transform: rotate(180deg) scale(1.2, 1.2);
}

/* end button */

@media only screen and (max-width: 1550px) {
	.home-body {
		-webkit-animation: animatedBackgroundMobile 5s linear 1;
		        animation: animatedBackgroundMobile 5s linear 1;
		-webkit-animation-fill-mode: forwards;
		        animation-fill-mode: forwards;
	}
	#str {
		font-size: 14vh;
	}
	h2 {
		font-size: 2rem;
	}
	#str {
		width: 58vw;
		height: 50vh;
	}
}

@media only screen and (max-width: 600px) {
	.home-body {
		-webkit-animation: animatedBackgroundMobile 5s linear 1;
		        animation: animatedBackgroundMobile 5s linear 1;
		-webkit-animation-fill-mode: forwards;
		        animation-fill-mode: forwards;
	}
	#str {
		font-size: 4rem;
	}
	h2 {
		font-size: 1rem;
		margin: 2rem;
		position: fixed;
		left: 40%;
		top: 70%;
		transform: translateX(-50%);
	}
	#str {
		height: 60vh;
		width: 50vw;
		position: fixed;
		left: 50%;
		top: 10%;
		transform: translateX(-50%);
	}
}
/* Search Bar */
.container {
	width: 92%;
	
}

h3{
	margin-top: 4rem;
	color:white;
	font-size: 3rem;
}

h4{
	color:white;
	font-size: 2.5rem;
}

.search-label {
	position: relative;
}

/* search input */
.container input {
	width: 100%;
	padding: 16px;
	font-size: 3rem;
	color: #444;
	outline: none;
	border: 0;
	border-bottom: 1px solid #f5f5f5;
}

/* search results */
.results-container {
	background-color: white;
	border-radius: 0 0 10px 10px;
}
.columns {
	font-size: 3vh;
	width: 100%;
	margin: 0;
	padding: 0;
}

.columns:hover {
	background:rgba(0,0,0,.1);
	border-top: 1px solid rgba(0,0,0,.1);
}

.search-icon {
	position: absolute;
	top: -10px;
	font-size: 24px;
	color: #555;
	right: 18px;
}

.album-image {
	width: 50px;
	border-radius: 10%;
}

.chosen-items-container{
    display: flex;
    justify-content: center;
	flex-wrap: wrap;
	margin: 1rem;
	
}

.chosen-items{
	display: flex;
    align-items: center;
    justify-content: space-evenly;
    background-color: rgba(255,255,255,.8);
    border-radius: 10px;
	padding: .2rem;
    width: 15rem;
	margin: .1rem;
	font-weight: bold;
}
.album-art {
  width: 5rem;
  height: 5rem;
  box-shadow: 3px 0 3px 0 rgba(0, 0, 0, .4);
  padding: 5px;
}

.cover {
  width: 100%;
  height: 100%;
  box-shadow: 0 0 3px 2px rgba(0, 0, 0, 0.25);
  margin-right: 2rem;
}

.track-info {
  margin: 2rem;
  line-height: 1;
}

.track-title {
  font-size: 2rem;
  color: black;
}

.track-title:hover {
  font-size: 2.1rem;
  color: #5a0a6f;
}

.track-artist {
  font-size: 1rem;
  color: #999
}

.audio-icons {
  height: 4rem;
}

/* Tooltips */

.tooltip {
  display: inline-block;
  position: relative;
  text-align: left;
}

.tooltip h3 {
  margin: 12px 0;
}

.tooltip .top {
  min-width: 170px;
  max-width: 300px;
  top: -5px;
  left: -13%;
  transform: translate(-30%, -100%);
  padding: 5px 21px;
  color: #ffffff;
  background-color: black;
  font-weight: normal;
  font-size: 14px;
  border-radius: 8px;
  position: absolute;
  z-index: 99999999;
  box-sizing: border-box;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.5);
  display: none;
}

.tooltip:hover .top {
  display: block;
}

.tooltip .top i {
  position: absolute;
  top: 100%;
  left: 30%;
  margin-left: -15px;
  width: 30px;
  height: 15px;
  overflow: hidden;
}

.tooltip .top i::after {
  content: '';
  position: absolute;
  width: 15px;
  height: 15px;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
  background-color: #009cdc;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.5);
}

/* end tooltip */

